
.parallax {
    background-image: -webkit-gradient(linear,left top, left bottom,from(transparent), color-stop(50%, transparent), to(white)),url("https://cdn.theculturetrip.com/images/56-3940160-1441141742b47c175bf5824bf29f6cb0b91be2bd55.jpg");
    background-image: -webkit-linear-gradient(top,transparent 0%, transparent 50%, white),url("https://cdn.theculturetrip.com/images/56-3940160-1441141742b47c175bf5824bf29f6cb0b91be2bd55.jpg");
    background-image: -o-linear-gradient(top,transparent 0%, transparent 50%, white),url("https://cdn.theculturetrip.com/images/56-3940160-1441141742b47c175bf5824bf29f6cb0b91be2bd55.jpg");
    background-image: linear-gradient(to bottom,transparent 0%, transparent 50%, white),url("https://cdn.theculturetrip.com/images/56-3940160-1441141742b47c175bf5824bf29f6cb0b91be2bd55.jpg");
    background-position: 55% 100%;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: .85;
}


